<template>
  <div class="container">
    <div class="row">
      <div v-for="symbol in learned" class="col-2 col-md-1">
        <div style="width: 100%;aspect-ratio: 1">
          <img style="width: 100%" :src="symbol.img">
        </div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Dictionary',
  props: {
  },
  mounted() {
    for(let s of this.$store.state.learned){
      if(this.learned.map(a=>a.key).includes(s))continue;
      this.learned.push({
        key: s,
        img: this.symbols[s].img,
        meaning: this.symbols[s].meaning
      })
    }
  },
  data(){
    return {
      symbols: {
        first_0:{
          img: require('@/assets/symbols/first/0.png'),
          meaning: 'Заклинание'
        },
        first_1:{
          img: require('@/assets/symbols/first/1.png'),
          meaning: 'Открыть'
        },
        first_2:{
          img: require('@/assets/symbols/first/2.png'),
          meaning: 'Портал'
        },
        first_3:{
          img: require('@/assets/symbols/first/3.png'),
          meaning: 'Дверь'
        }
      },
      learned: []
    }
  },
  methods: {
  },
  watch: {
    '$store.state.learned':{
      handler(val){
        for(let s of val){
          if(this.learned.map(a=>a.key).includes(s))continue;
          this.learned.push({
            key: s,
            img: this.symbols[s].img,
            meaning: this.symbols[s].meaning
          })
        }
      },
      deep: true
    }
  }
}
</script>
