import { createStore } from 'vuex';
import router from "@/router";

const store = createStore({
    state() {
        return {
            learned: []
        };
    },
    mutations: {
        learn(state, symbols) {
            for(let s of symbols)
                if(!state.learned.includes(s))
                    state.learned.push(s)
        }
    }
});

export default store