<template>
  <div v-if="data.type==='cutscene'" @click="clicked">
    <img v-if="act.bg" :src="act.bg" alt="Вы не должны были это увидеть">
    <div>{{act.text}}</div>
  </div>
</template>

<script>
export default {
  name: 'Scene',
  props: {
    data: {
      type: Object,
      default: () => ({
        type: 'none'
      })
    }
  },
  data(){
    return {
      current: 0,
    }
  },
  computed: {
    act(){
      return this.data.acts[this.current]
    }
  },
  methods: {
    clicked(){
      for(let method of this.act.click){
        (({
          goTo: this.goTo,
          learn: this.learn
        }[method.method])||(()=>{}))(...method.args)
      }
    },
    goTo(page){
      this.current = page
    },
    learn(symbols){
      this.$store.commit('learn',symbols)
    }
  }
}
</script>

